    <!--<Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>-->
    <!--</div>-->
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data">
    </Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  data () {
    return {
      formData: {},
      load: false,
      space: [],
      listData: {},
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '模板编号',
          align: 'center',
          key: 'template_no'
        },
        {
          title: '模板名称',
          align: 'center',
          key: 'name'
        },
        {
          title: '模板备注',
          align: 'center',
          key: 'remark'
        },
        {
          title: '更新时间',
          align: 'center',
          key: 'update_time'
        },
        {
          title: 'Action',
          width: 250,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.detail(params.row.id)
                  }
                }
              }, '详情')
            ])
          }
        }
      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      this.load = true
      api.index({ template_id: this.$route.params.id }, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },
    detail (id) {
      this.$router.push({
        name: 'contract_template_history_detail',
        params: {
          id: this.$route.params.id,
          historyId: id
        }
      })
    },

    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.name + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
